import * as GWM from 'gw-map';
import * as BLUE from '../blueprint';
export class MachineHorde extends GWM.horde.Horde {
    constructor(config) {
        super(config);
        this.machine = null;
        this.machine = config.blueprint || null;
    }
    _addLeader(leader, map, x, y, opts) {
        if (this.machine) {
            BLUE.build(this.machine, map, x, y);
        }
        if (!super._addLeader(leader, map, x, y, opts))
            return false;
        return true;
    }
}
